.ListItem {
    list-style-type: none;
    text-align: left;
    font-size: 1.5rem;
    display: grid;
    gap: 0.5rem;
    border-radius: 10px;
    padding: 1rem;
    background-color: rgb(33, 32, 36);
    box-shadow: inset 1px 1px 5px 0px rgb(24, 24, 24);
  }
  
  .TopContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    word-wrap: break-word;
  }
  
  .NameContainer {
    word-break: break-all;
  }
  
  .BotContainer {
    display: flex;
    flex-direction: row;
    gap: 0.7rem;
    margin-right: 0.5rem;
    transition: all 0.3s ease;
    align-items: center;
  }
  
  .VoteBar {
    border-radius: 10px;
    background-color: #CBB028;
    transition: all 0.3s ease;
    height: 50%;
  }
  
  .VoteCountContainer {
    border-radius: 10px;
    background-color: rgb(22, 23, 27);
    padding: 0 0.5rem 0 0.5rem;
    box-shadow: inset 1px 1px 5px 0px rgb(24, 24, 24);
  }
  